<template>
  <v-theme-provider class="outer" :theme=this.customTheme>
    <v-app class="body">
      <v-app-bar absolute v-if="smAndUp" color="transparent" elevation="0">
        <v-app-bar-title>
          <div class="d-flex">
            <v-btn @click=handleTheme()>
              <v-icon size="25">{{ this.themeIcon }}</v-icon>
            </v-btn>
            <v-btn class="mr-1" @click="scrollToElement('AboutMe')">About Me</v-btn>
            <v-btn class="mr-1" @click="scrollToElement('Projects')">Projects</v-btn>
            <v-btn class="mr-1" @click="scrollToElement('Skills')">Skills</v-btn>
            <v-btn class="mr-1" @click="scrollToElement('Contact')">Contact</v-btn>
          </div>
        </v-app-bar-title>
      </v-app-bar>
      <v-app-bar absolute v-else color="transparent" elevation="0">
        <div class="d-flex">
          <v-btn class="ml-n2 mt-n2" :icon=this.themeIcon @click=handleTheme()>
          </v-btn>
          <v-btn class="btn-text" @click="scrollToElement('AboutMe')">About Me</v-btn>
          <v-btn class="btn-text" @click="scrollToElement('Projects')">Projects</v-btn>
          <v-btn class="btn-text" @click="scrollToElement('Skills')">Skills</v-btn>
          <v-btn class="btn-text" @click="scrollToElement('Contact')">Contact</v-btn>
        </div>
      </v-app-bar>
      <v-main class="body">
        <v-container fluid>
          <InfoComponent />
          <AboutMe />
          <ProjectComponent />
          <SkillsComponent />
          <ContactMe />
        </v-container>
      </v-main>
      <CustomFooter />
    </v-app>
  </v-theme-provider>
</template>

<script>
import InfoComponent from './components/InfoComponent.vue';
import ProjectComponent from './components/ProjectComponent.vue';
import CustomFooter from './components/CustomFooter.vue';
import AboutMe from './components/AboutMe.vue';
import ContactMe from './components/ContactMe.vue';
import SkillsComponent from './components/SkillsComponent.vue';
import { useDisplay } from 'vuetify';

export default {
  name: 'App',

  components: {
    InfoComponent,
    ProjectComponent,
    CustomFooter,
    AboutMe,
    ContactMe,
    SkillsComponent
  },
  setup() {
    // Destructure only the keys we want to use
    const { xs, mdAndUp, smAndUp } = useDisplay();

    return { xs, mdAndUp, smAndUp }
  },

  methods: {

    handleTheme() {
      if (this.customTheme === "customLightTheme") {
        this.customTheme = "customDarkTheme";
        this.themeIcon = 'mdi-brightness-3';
      } else {
        this.customTheme = "customLightTheme";
        this.themeIcon = 'mdi-brightness-4';
      }
    },
    /*
    goto(refName) {
      var element = this.$refs[refName];
      var top = element.offsetTop;
      window.scrollTo(0, top);
    },*/
    scrollToElement(divId) {
      const element = document.getElementById(divId);
      element.scrollIntoView({ behavior: 'smooth' });
    },

  },
  data: () => ({
    drawer: false,
    icons: [
      'mdi-linkedin',
    ],
    themeIcon: 'mdi-brightness-4',
    customTheme: "customLightTheme",
  }),
  watch: {
    group() {
      this.drawer = false
    },
  },
}
</script>
<style>
.outer {
  overflow: hidden;
  position: relative;
}


.body {
  overflow: hidden;
  background-color: rgb(var(--v-theme-background));
  /*background-image: linear-gradient(rgb(var(--v-theme-background)), rgb(var(--v-theme-footer)));*/
  /*background-image:  linear-gradient( rgb(var(--v-theme-background)) 14%, rgb(var(--v-theme-background-2)) 39%, rgb(var(--v-theme-yellow)) 66%, rgb(var(--v-theme-footer)) 100%);*/
  background-image: linear-gradient(rgb(var(--v-theme-background)) 35%, rgb(var(--v-theme-footer)) 95%);
  width: 100%;
}

.btn-text {
  font-size: 8pt;
  width: 75px;
  margin-right: 3px;
}

.btn-color-background {
  background-color: rgb(var(--v-theme-btn));
}

.center-items {
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
}
</style>
